var randomNumber = require('./random').number;

module.exports = function (mask) {
  var mod = function mod(dividend, divisor) {
    return Math.round(dividend - Math.floor(dividend / divisor) * divisor);
  };

  var n = 9;
  var digits = [];

  for (var i = 0; i < n; i += 1) {
    digits.push(randomNumber(n));
  }

  var d1 = digits.reduce(function (t, c, i) {
    return t + c * (10 - i);
  }, 0);
  d1 = 11 - mod(d1, 11);
  if (d1 > 9) d1 = 0;
  var d2 = digits.reduce(function (t, c, i) {
    return t + c * (11 - i);
  }, d1 * 2);
  d2 = 11 - mod(d2, 11);
  if (d2 > 9) d2 = 0;
  var cpf = digits.reduce(function (t, curr) {
    return "".concat(t).concat(curr);
  }, '').concat("".concat(d1), "".concat(d2));
  if (mask) return cpf.replace(/(\d{3})(\d{3})(\d{3})(\d{2})/g, '$1.$2.$3-$4');
  return cpf;
};